.navigation {
  font-weight: 500;
  font-size: 24px;
  line-height: 1.3;
  color: #0b0b0b;

  &__container {
    display: flex;
  }

  &__link {
    display: inline-block;
    padding: 40px;
    width: 100%;
    text-align: center;
    background-color: #f1f0f0;

    &:not(:last-child) {
      margin-right: 40px;
    }
  }
}